.journey {
    padding-left: 60px;
    padding-right: 60px;

    .header {
        position: fixed;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-style: solid;
        border-width: 0px 0px 0px 0px;
        border-color: #FF049C;
        box-shadow: 0px 15px 15px -15px #ff049c;
        top: 0px;
        background-color: black;
        width: 100%;
        margin-left: -60px;
        margin-right: -60px;
        padding-left: 60px;
        padding-right: 60px;
        transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;

        img {
            width: 200px;
        }

        .navbar {
            display: flex;
            align-items: center;

            a:not(:last-child) {
                margin-right: 90px;
                font-family: '28 Days Later', sans-serif;
                font-size: 35px;
                line-height: 43px;
                color: white;
            }

            a:selected {
                color: #A53E6F;
            }

            a:last-child {
                font-family: 'Segoe-UI', sans-serif;
                font-size: 20px;
                line-height: 49px;
                color: white;
                display: flex;
                align-items: center;
                background-color: #A53E6F;
                width: 253px;
                height: 68px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 52px;

                svg {
                    margin-left: 43px;
                }
            }
        }
    }

    .section-1 {
        display: flex;
        justify-content: flex-end;
        margin-top: 30px;

        .contact-group {
            display: flex;
            align-items: center;
        }
    }

    .description {
        font-family: '28 Days Later', sans-serif;
        font-size: 43px;
        line-height: 52px;
        text-align: center;
    }

    .section-2 {
        display: flex;
        margin-top: 80px;
        align-items: flex-end;

        .part-1 {
            width: 50%;
            margin-right: 50px;

            .title {
                font-family: '28 Days Later', sans-serif;
                font-size: 60px;
                line-height: 86px;
                text-align: center;
            }

            img {
                margin-top: 50px;
                box-shadow: 0px 0px 10px #FF049C;
                border: 15px solid #FF049C;
                border-radius: 25px;
            }
        }
    }

    .section-2 .part-2,
    .section-3,
    .section-5 .part-1,
    .section-5 .part-2 .part {
        box-shadow: 0px 0px 10px #FF049C;
        border: 15px solid #FF049C;
        border-radius: 25px;
        padding-top: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;

        .title1 {
            display: flex;
            align-items: center;
            font-family: "Segoe UI", Sans-serif;
            font-weight: 700;
            font-size: 22px;
            margin-left: 20px;

            .rect {
                border-radius: 50%;
                width: 10px;
                height: 10px;
                background-color: white;
                margin-right: 10px;
                font-size: 18px;
            }
        }

        .content1 {
            margin-left: 40px;
            font-family: "Segoe UI", Sans-serif;
            font-weight: 400;
            margin-bottom: 50px;
            font-size: 18px;
        }

        .stage {
            border: 15px solid #FF049C;
            border-radius: 25px;
            margin-left: -15px;
            margin-right: -15px;
            background-color: #A53E6F;
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: '28 Days Later', sans-serif;
            font-size: 59px;
            line-height: 71px;
            margin-bottom: -15px;
            position: relative;
            height: 150px;

            
        }
    }

    .section-3 {
        width: 100%;
        margin-top: 50px;
        position: relative;

        img {
            position: absolute;
            width: 250px;
            top: -300px;
            right: 30px;
        }
    }

    .section-4 {
        border: 15px solid #A53E6F;
        border-radius: 25px;
        margin-top: 50px;
    }

    .section-5 {
        display: flex;
        margin-top: 50px;

        >div {
            width: 50%;
        }

        .part-1 {
            margin-right: 50px;
        }

        .part-2 {
            display: flex;
            flex-direction: column;

            .part {
                width: 100%;
            }

            .title {
                font-family: '28 Days Later', sans-serif;
                font-size: 60px;
                line-height: 86px;
                margin-top: 150px;
                text-align: center;
            }
        }
    }

    .meet-team {
        margin-top: 220px;
        font-family: '28 Days Later', sans-serif;
        font-size: 60px;
        line-height: 86px;
        text-align: center;
    }

    .section-6, .section-7 {
        display: flex;
        justify-content: space-between;
        margin-top: 58px;

        .team-item {
            display: flex;
            flex-direction: column;
            align-items: center;

            >img {
                width: 180px;
                height: 180px;
                border-radius: 50%;
            }

            .role {
                margin-top: 30px;
                font-family: 'Segoe-UI', sans-serif;
                font-size: 32px;
                line-height: 38px;
            }

            a {
                margin-top: 15px;

                img {
                    width: 31px;
                    height: 25px;
                }
            }
        }
    }

    .section-7 {
        justify-content: space-around;
    }

    .elementor-divider-separator:before {
        margin-left: -60px;
    }

    .elementor-divider-separator::after {
        margin-right: -60px;
    }
}

@media only screen and (max-width: 1600px) {
    .journey .section-2 .part-1 .title, .journey .section-5 .part-2 .title, .journey .meet-team {
        font-size: 50px;
        line-height: 70px;
    }

    .journey .section-3 img {
        width: 200px;
        top: -240px
    }
}

@media only screen and (max-width: 1350px) {
    .journey .section-2 .part-1 .title, .journey .section-5 .part-2 .title, .journey .meet-team {
        font-size: 40px;
        line-height: 50px;
    }

    .journey .section-3 img {
        width: 150px;
        top: -180px;
    }

    .journey .section-2 .part-2 .stage, .journey .section-3 .stage, .journey .section-5 .part-1 .stage, .journey .section-5 .part-2 .part .stage {
        height: 100px;
    }

    .journey .section-6 .team-item > img, .journey .section-7 .team-item > img {
        width: 150px;
        height: 150px;
    }

    .journey .section-6 .team-item .role, .journey .section-7 .team-item .role {
        font-size: 22px;
        margin-top: 10px;
    }

    .journey .section-6 .team-item a, .journey .section-7 .team-item a {
        margin-top: 8px;
    }
}

@media only screen and (max-width: 1150px) {
    .journey .section-2 .part-1 .title, .journey .section-5 .part-2 .title, .journey .meet-team {
        font-size: 30px;
        line-height: 40px;
    }

    .journey .description {
        font-size: 33px;
    }

    .journey .section-3 img {
        width: 100px;
        top: -120px
    }

    .journey .section-2 .part-2 .stage, .journey .section-3 .stage, .journey .section-5 .part-1 .stage, .journey .section-5 .part-2 .part .stage {
        height: 80px;
        font-size: 40px;
        line-height: 50px;
        border-width: 8px;
        margin-left: -8px;
        margin-right: -8px;
        margin-bottom: -8px;
    }

    .journey .section-2 .part-2, .journey .section-3, .journey .section-5 .part-1, .journey .section-5 .part-2 .part {
        border-width: 8px;
    }

    .journey .section-2 .part-2 .title1, .journey .section-3 .title1, .journey .section-5 .part-1 .title1, .journey .section-5 .part-2 .part .title1 {
        font-size: 16px;
    }

    .journey .section-2 .part-2 .content1, .journey .section-3 .content1, .journey .section-5 .part-1 .content1, .journey .section-5 .part-2 .part .content1 {
        font-size: 16px;
    }
}

@media only screen and (max-width: 900px) {
    .journey .section-2 .part-1 .title, .journey .section-5 .part-2 .title, .journey .meet-team {
        font-size: 20px;
        line-height: 30px;
    }
    
    
}